<template>
    <v-container class="pa-0">
        <!-- Header -->
        <PDHeader></PDHeader>

        <!-- Main -->
        <v-main class="pt-5">
            <v-container>
                <v-row>
                    <v-col cols="12">
                        <h1>常見問題</h1>
                    </v-col>
                </v-row>

                <v-row>
                    <v-col cols="12" md="6" v-for="item in useStore().state.question_list" :key="item.num">
                        <v-card @click="goquestion(item)">
                            <v-card-title>
                                <div>
                                    <span class="headline">{{ item.question.substring(0, 10) + "..." }}</span>
                                </div>
                            </v-card-title>
                            <v-card-text>
                                {{ item.answer.substring(0, 20) + "..." }}
                            </v-card-text>
                        </v-card>
                    </v-col>
                </v-row>

            </v-container>
        </v-main>

        <!-- Footer -->
        <PDFooter></PDFooter>
    </v-container>
</template>

<script setup>
// components
import PDHeader from '@/components/PD-Header.vue';
import PDFooter from '@/components/PD-Footer.vue';
import { useStore } from 'vuex';
import router from '@/router';

// 假資料
const store = useStore()
const goquestion = (item) => {
    store.commit('set_question_num', item.num)
    router.push('/questiondetail');
}



</script>
